<template>
  <div class="login-wrap">
    <div class="ms-title"></div>
    <div class="ms-login">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="0px"
        class="demo-ruleForm"
      >
        <el-form-item prop="username">
          <el-input v-model="ruleForm.username" placeholder="username"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            type="password"
            placeholder="password"
            v-model="ruleForm.password"
            @keyup.enter.native="submitForm('ruleForm')"
          ></el-input>
        </el-form-item>
        <el-form-item prop="code" label="">
          <el-input type="text" v-model="ruleForm.code" placeholder="- - - -">
            <template slot="append">
              <div @click="updateCaptcha" class="captcha" v-html="captcha"></div>
            </template>
          </el-input>
        </el-form-item>
        <div class="login-btn">
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        </div>
        <p style="font-size:12px;line-height:30px;color:#999;">Tips : </p>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      ruleForm: {
        username: "admin",
        password: "123456",
        code: ""
      },
      captcha: null,
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" }
        ],
        password: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    // 
    getCaptcha() {
      this.$http.get("http://127.0.0.1:7001/getCaptcha").then(res => {
        this.captcha = res.data;
        console.log(res.data);
      });
    },
    submitForm(formName) {
      console.log(111);

      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$http
            .post("http://127.0.0.1:7001/login", {
              username: this.ruleForm.username,
              password: this.ruleForm.password,
              code: this.ruleForm.code
            })
            .then(res => {
              console.log(res);
            });
          localStorage.setItem("ms_username", this.ruleForm.username);
          this.$router.push("/");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    updateCaptcha() {
      this.getCaptcha();
    }
  },
  mounted() {
    this.getCaptcha();
  }
};
</script>

<style scoped>
.login-wrap {
  position: relative;
  width: 100%;
  height: 100%;
}
.ms-title {
  position: absolute;
  top: 50%;
  width: 100%;
  margin-top: -230px;
  text-align: center;
  font-size: 30px;
  color: #fff;
}
.ms-login {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 300px;
  height: 160px;
  margin: -150px 0 0 -190px;
  padding: 40px;
  border-radius: 5px;
  background: #fff;
}
.login-btn {
  text-align: center;
}
.login-btn button {
  width: 100%;
  height: 36px;
}
</style>