<template>
  <div class="main_wrap">
    <div class="editor_wrap">
      <div class="title_wrap">
        <label></label>
        <el-input v-model="title" placeholder=""></el-input>
        <label></label>
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 6}"
          placeholder=""
          v-model="textarea"
        ></el-input>
      </div>
      <quill-editor
        v-model="content"
        ref="myQuillEditor"
        :options="editorOption"
        @blur="onEditorBlur($event)"
        @focus="onEditorFocus($event)"
        @change="onEditorChange($event)"
      ></quill-editor>
      <el-button type="primary" @click="saveArticle()"></el-button>
    </div>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor";
// require styles
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
export default {
  data() {
    return {
      title: "",
      textarea: "",
      content: null,
      editorOption: {}
    };
  },
  components: {
    quillEditor
  },
  methods: {
    onEditorBlur() {
      //
      console.log(this.content);
    },
    onEditorFocus() {
      //
    },
    onEditorChange() {
      //
    },
    saveArticle(){
      this.$http.post("http://127.0.0.1:7001/saveArticle",{
        title:this.title,
        detail:this.textarea,
        content:this.content,
      }).then(res=>{
        console.log(res);
        
      })
    }
  }
};
</script>
<style>
.editor_wrap{
  min-height: 100%;
}
.title_wrap{
  margin-bottom: 20px;
}
.ql-container.ql-snow {
  height: 50vh;
}
</style>
